/**
 * @module       RD Navbar
 * @description  Enables RD Navbar Plugin
 */

;(function ($) {

    $(document).ready(function () {
        var o = $('.rd-navbar');
        var headerNav = $('#header-nav');
        var skipNav = $('.skip-link.skip-nav');
        if (headerNav.length > 0) {
            headerNav.removeClass('skip-content');
        }
        if (skipNav.length > 0) {
            skipNav.remove();
        }

        if (o.length > 0) {
            o.RDNavbar({
                stickUp: true,
                stickUpOffset: '178',
                responsive: {
                 0: {
                    layout: 'rd-navbar-fixed',
                    stickUpClone: false,
                    focusOnHover: false
                 },
                 768: {
                    layout: 'rd-navbar-fixed',
                    stickUpClone: false,
                    focusOnHover: false
                 },
                 991: {
                    layout: 'rd-navbar-static',
                    stickUpClone: false,
                    stickUpOffset: '273',
                 },
                 1200: {
                    layout: 'rd-navbar-static',
                    stickUpClone: false,
                    stickUpOffset: '178',

                 }
              }
            });         
        }
    
    });

})(jQuery);


